//加载loading动画
function loading($rootScope,$timeout){
    $rootScope.loading=true;    //加载动画
    $timeout(function () {
        closeLoading($rootScope);
    }, 300);
}
//关闭加载loading动画
function closeLoading($rootScope){
    $rootScope.loading=false;
}

